.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 40px;
  height: 100vh;
  color: white;
}

.hero-section {
  background: linear-gradient(
    135deg,
    #fceabb,
    #f8b500
  ); /* light orange-yellow */
  color: #2d2d2d; /* dark text for better contrast */
}
.marquee-text {
  font-size: 1rem;
  margin-top: 20px;
  color: #333;
  font-weight: 500;
}
.title-glow {
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 0 0 8px #fff, 0 0 16px #ffecb3; /* soft yellow glow */
}

.problem-section {
  background: #1e1e2f;
}

.promise-section {
  background: #0f2027;
}

.cta-section {
  background: black;
  color: #00ffd0;
}

.title-glow {
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 0 0 10px #fff, 0 0 20px #00f0ff;
}

.marquee-text {
  font-size: 1.2rem;
  margin-top: 20px;
  color: yellow;
}

.cta-button {
  background-color: #00ffd0;
  color: #000;
  padding: 12px 24px;
  font-size: 1.2rem;
  font-weight: bold;
  border: none;
  border-radius: 25px;
  margin-top: 20px;
  cursor: pointer;
  transition: transform 0.3s;
}

.cta-button:hover {
  transform: scale(1.1);
}
#fullpage-wrapper,
.section {
  height: 100vh !important;
}

/* Fix fullpage.js overflow issue */
html.fp-enabled,
html.fp-enabled body {
  overflow: hidden !important;
}
